.checkbox {
  appearance: none;
  background-color: var(--utility-neutral-0);
  border-radius: var(--border-radius-s);
  border: 2px solid var(--utility-neutral-50);
  display: grid;
  height: 20px;
  margin: var(--spacing-xs);
  place-content: center;
  width: 20px;
}
.checkbox:checked {
  background-color: var(--theme-a-3);
}
.checkbox:checked::before {
  background-color: var(--utility-neutral-0);
  transform: scale(1);
}
.checkbox:hover:not(:disabled), .checkbox:not(:disabled):checked {
  border-color: var(--theme-a-3);
}
.checkbox::before {
  content: "";
  width: 0.65em;
  height: 0.65em;
  clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  transform: scale(0);
  transform-origin: bottom left;
  transition: 120ms transform ease-in-out;
  background-color: var(--utility-neutral-0);
}
.checkbox:focus-visible {
  border-radius: 2px;
  outline: 2px solid var(--theme-a-3);
  transform: scale(1.05);
}
.checkbox:disabled {
  color: var(--utility-neutral-50);
  cursor: not-allowed;
}